<template>
  <div>
    <h1></h1>
    <!--  -->
    <el-table :data="items">
      <!-- items._id -->
      <el-table-column prop="id" label="ID"></el-table-column>

      <el-table-column prop="introduction" label=""></el-table-column>

      <el-table-column fixed="right" label="">
        <template slot-scope="scope">
          <!-- scope.row  -->
          <el-button
            type="text"
            size="small"
            @click="$router.push(`/case/edit/${scope.row.id}`)"
            ></el-button
          >
          <el-button type="text" size="small" @click="remove(scope.row)"
            ></el-button
          >
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      items: [],
    };
  },
  methods: {
    async fetchExample() {
      const res = await this.$http.get(`/display_examples`);
      // console.log(res.data.data);
      this.items = res.data.data;
    },
    async remove(row) {
      console.log(row.id);
      this.$confirm(`"${row.introduction}"`, "", {
        confirmButtonText: "ok",
        cancelButtonText: "cancel",
        type: "warning",
      }).then(async () => {
        const res = await this.$http.get(`/delete_example?exampleId=${row.id}`);
        this.$message({
          type: "success",
          message: "",
        });
        this.fetchExample();
      });
    },
  },
  created() {
    this.fetchExample();
  },
};
</script>
