<template>
  <div>
    <top></top>
    <banner></banner>
    <my-nav></my-nav>
    <img src="../../assets/images/index2.jpg" style="width:100%" alt />
    <div class="case-content">
      <left-side title=""></left-side>
      <el-row class="detial" style="width:90%">
        <el-row>
          <el-divider content-position="left"></el-divider>
          <el-col :span="8">
            <div class="block">
              <el-image
                style="width: 200px; height: 300px"
                :fit="fit"
                :src="model.avatarUrl"
              ></el-image>
            </div>
          </el-col>
          <el-col :span="5">
            <el-form>
              <el-form-item size="medium" label="">
                <span>{{ model.account }}</span>
              </el-form-item>
              <el-form-item size="medium" label="">
                <span>{{ model.realName }}</span>
              </el-form-item>
              <el-form-item size="medium" label="">
                <span>{{ model.collegeName }}</span>
              </el-form-item>
              <el-form-item size="medium" label="">
                <span>{{ model.majorName }}</span>
              </el-form-item>
              <el-form-item size="medium" label="">
                <span>{{
                  model.status == 0
                    ? ""
                    : model.status == 1
                    ? ""
                    : ""
                }}</span>
              </el-form-item>
              <el-form-item size="medium" label="">
                <span>{{ model.email }}</span>
              </el-form-item>
            </el-form>
          </el-col>
        </el-row>
        <el-divider content-position="left"></el-divider>
        <el-row>
          <el-form @submit.native.prevent="save">
            <el-form-item label="">
              <el-upload
                class="avatar-uploader"
                :action="'http://49.232.138.118:8080/yunzhi/user/upload_image'"
                :show-file-list="false"
                :before-upload="beforeAvatarUpload"
                :on-success="afterUpload"
              >
                <img
                  v-if="model.avatarUrl"
                  :src="model.avatarUrl"
                  class="avatar"
                />
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="saveAvatar"></el-button>
            </el-form-item>
          </el-form>
          <el-col :span="4">
            <el-button type="primary" @click="editDialogVisible = true"
              ></el-button
            >
          </el-col>
        </el-row>
      </el-row>
    </div>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      @closed="editDialogClosed"
      width="30%"
    >
      <el-form ref="editFormRef" label-width="70">
        <el-row>
          <el-col :span="24">
            <el-form-item label="" prop="mobile">
              <el-input v-model="password"></el-input> </el-form-item
          ></el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item label="" prop="mobile">
              <el-input v-model="repassword"></el-input> </el-form-item
          ></el-col>
        </el-row>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editPassword"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import Top from "../../components/Home/Top";

import Banner from "../../components/Banner";
import Nav from "../../components/Nav";
import SideBar from "../../components/SideBar";
export default {
  components: {
    Top,
    Banner,
    MyNav: Nav,
    LeftSide: SideBar,
  },
  data() {
    return {
      model: {},
      active: false,
      fit: "fill",
      // 
      editDialogVisible: false,
      password: "",
      repassword: "",
    };
  },
  methods: {
    async saveAvatar() {
      let uuid = this.$route.params.uuid;
      const res = await this.$http.get(
        `http://49.232.138.118:8080/yunzhi/user/keep?uuid=${uuid}&avatarUrl=${this.model.avatarUrl}`
      );
      this.$message.success("");
    },
    async fetch() {
      //49.232.138.118:8080/yunzhi/admin/detail_user?uuid=5
      let uuid = this.$route.params.uuid;
      const res = await this.$http.get(
        `http://49.232.138.118:8080/yunzhi/user/center?uuid=${uuid}`
      );

      this.model = res.data.data;
      console.log(this.model);
    },
    afterUpload(res) {
      console.log(res);
      this.$set(this.model, "avatarUrl", res.data);
      console.log(this.model.avatarUrl);
    },
    beforeAvatarUpload(file) {
      const isLt3M = file.size / 1024 / 1024 < 3;

      if (!isLt3M) {
        this.$message.error(" 3MB!");
      }
      return isLt3M;
    },
    // 
    async showEditDialog(id) {
      this.editDialogVisible = true;
    },
    // 
    editDialogClosed() {
      this.$refs.editFormRef.resetFields();
      this.editDialogVisible = false;
    },
    async editPassword() {
      if (this.password == "") {
        return this.$message.error("");
      }
      if (this.password !== this.repassword) {
        return this.$message.error("");
      }
      const uuid = this.$route.params.uuid;
      const data = { uuid: uuid, password: this.password };
      const res = await this.$http.post(
        `http://49.232.138.118:8080/yunzhi/user/change_pw`,
        data
      );
      this.$message.success("");
      console.log(res);
      this.editDialogVisible = false;
    },
  },
  created() {
    this.fetch();
  },
};
</script>

<style scoped>
body {
  margin: 0;
  padding: 0;
}
.case-content {
  width: 1080px;
  margin: 0 auto;
  display: flex;
  /* justify-content: space-between; */
}
.teacher {
  flex: 1;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  padding: 40px;
}
.pic {
  width: 33%;

  padding: 25px;
  text-align: center;
  /* margin: 10px; */
}

.bottom {
  margin-top: 13px;
  line-height: 12px;
}

.image {
  width: 100%;
  display: block;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}

.clearfix:after {
  clear: both;
}
.detial {
  margin-top: 50px;
  box-shadow: 0 0 5px black;
  padding: 50px;
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>
