<template>
  <div class="register-container">
    <div class="register">
      <div class="title"></div>
      <el-divider></el-divider>

      <!-- form-> -->
      <el-form ref="form" class="content" :model="form" label-width="80px">
        <el-form-item label="">
          <el-select v-model="form.status" placeholder="">
            <el-option
              v-for="item in identity"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="">
          <el-input
            placeholder=""
            v-model="form.account"
          ></el-input>
        </el-form-item>

        <el-form-item label="">
          <el-input placeholder="" v-model="form.realName"></el-input>
        </el-form-item>

        <el-form-item label="">
          <el-input placeholder="" v-model="form.password"></el-input>
        </el-form-item>

        <el-form-item label="">
          <el-input placeholder="" v-model="repassword"></el-input>
        </el-form-item>

        <el-form-item label="">
          <el-input placeholder="" v-model="form.email"></el-input>
          <el-button @click="sendEmail" style="margin-top:10px"
            ></el-button
          >
        </el-form-item>

        <el-form-item label="">
          <el-input placeholder="" v-model="reemail"></el-input>
        </el-form-item>

        <el-form-item label="">
          <el-select v-model="collegeName" placeholder="">
            <el-option
              v-for="item in collegeList.data"
              :key="item.id"
              :label="item.collegeName"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="">
          <el-select v-model="form.major" placeholder="">
            <el-option
              v-for="item in acadamyList.data"
              :key="item.id"
              :label="item.majorName"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item>
          <el-button type="warning" size="medium" @click="onSubmit"
            ></el-button
          >
        </el-form-item>
      </el-form>

      <router-link to="/login">
        <el-link type="primary" style="margin-left: 80px;"
          ></el-link
        >
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      form: {
        //  0  1 
        // accoutn:'',
        // realName:'',
        // nickName:'',
        // status: null,
        // email: ""
      },
      // 
      email: {},
      repassword: "",
      reemail: "",
      // 
      collegeList: {},
      // 
      collegeName: "",
      // ,
      acadamyList: {},

      // 
      identity: [
        {
          value: 0,
          label: "",
        },
        {
          value: 1,
          label: "",
        },
      ],

      major: [
        {
          value: "chinese",
          label: "",
        },
        {
          value: "art",
          label: "",
        },
      ],
      value: "",
    };
  },
  methods: {
    async onSubmit() {
      // console.log("submit!");
      if (
        this.email.data === this.reemail &&
        this.form.password === this.repassword
      ) {
        console.log(12312312);
        const res = await this.$http.post(`/user/register`, this.form);
        console.log(res.data.status);
        if (res.data.status == 0) {
          this.$message.error("-");
        } else {
          this.$message({
            type: "success",
            message: "",
          });
          this.$router.push("/login");
        }
      }
    },

    // 
    sendEmail() {
      this.fetchEmail(this.form.email);
    },
    // 
    async fetchEmail(email) {
      const res = await this.$http.get(`/user/email?email=${email}`);
      this.email = res.data;
      if (this.email.status === 1) {
        this.$message({
          type: "success",
          message: "",
        });
      }
      // console.log(this.email);
    },
    // 
    async fetchCollage() {
      const res = await this.$http.get(`/user/getAcadamy`);
      this.collegeList = res.data;
      console.log(this.collegeList.data);
    },
    // 
    async fetchAcadamy(id) {
      const res = await this.$http.get(`/user/getMajor?acadamyId=${id}`);
      this.acadamyList = res.data;
    },
  },
  watch: {
    collegeName: function(newVal) {
      this.fetchAcadamy(newVal);
      // console.log(newVal);
    },
  },
  created() {
    this.fetchCollage();
  },
};
</script>

<style scoped>
.register-container {
  display: flex;
  height: 100%;
  /* width: 100vh; */
  justify-content: center;
  align-items: center;
}
.title {
  padding-left: 30px;
  margin-bottom: 40px;
  font-size: 24px;
  /* font-weight: bold; */
}
.content {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
}
.register {
  width: 350px;

  /* background-color: red; */
  box-shadow: 0px 0px 2px grey;
  border-radius: 20px;
  padding: 20px;
}
</style>
